import { h, ComponentInternalInstance, Slots, VNode, setBlockTracking } from 'vue';
import camelCase from 'lodash/camelCase';

interface JSXRenderContext {
  defaultNode?: VNode;
  params?: Record<string, any>;
}

/**
 * Ê∏≤Êüì TNodeÔºåprops Âíå ÊèíÊßΩÂêåÊó∂Â§ÑÁêÜ„ÄÇ‰∏é renderTNodeJSX Âå∫Âà´Âú®‰∫é Â±ûÊÄßÂÄº‰∏∫ undefined Êó∂‰ºöÊ∏≤ÊüìÈªòËÆ§ËäÇÁÇπ
 * @param instance ÁªÑ‰ª∂Á§∫‰æã
 * @param name ÊèíÊßΩÂíåÂ±ûÊÄßÂêçÁß∞
 * @param options Áº∫ÁúÅÊèíÊßΩÂÜÖÂÆπ
 * @example renderTNode(getCurrentInstance(), 'closeBtn')
 * @example renderTNode(getCurrentInstance(), 'closeBtn', <t-icon-close />)„ÄÇthis.closeBtn ‰∏∫Á©∫Êó∂ÔºåÂàôÂÖúÂ∫ïÊ∏≤Êüì <t-icon-close />
 */
export const renderTNode = (
  instance: ComponentInternalInstance | null,
  name: string,
  options?: Slots | JSXRenderContext,
): any => {
  if (instance === null) {
    return h('', null);
  }

  const params = typeof options === 'object' && 'params' in options ? options.params : null;
  const defaultNode = typeof options === 'object' && 'defaultNode' in options ? options.defaultNode : options;
  let propsNode: any;
  if (name in instance.props || camelCase(name) in instance.props) {
    propsNode = instance.props[name] || instance.props[camelCase(name)];
  }
  // if (propsNode === false) return;

  // ÂêåÂêç‰ºòÂÖàÂ§ÑÁêÜÊèíÊßΩ
  if (instance.slots[name]) {
    return instance.slots[name]?.call(params);
  }

  if (propsNode === true && defaultNode) {
    return instance.slots[name] ? instance.slots[name]?.call(params) : defaultNode;
  }

  if (typeof propsNode === 'function') {
    setBlockTracking(-1);
    const vnode = propsNode(h, params);
    setBlockTracking(1);
    return vnode;
  }

  const isPropsEmpty = [undefined, params, ''].includes(propsNode);
  if (isPropsEmpty && instance.slots[name]) return instance.slots[name]?.call(params);
  return propsNode;
};

/**
 * Áî®‰∫éÂ§ÑÁêÜÁõ∏ÂêåÂêçÁß∞ÁöÑ TNode Ê∏≤Êüì
 * @param vm ÁªÑ‰ª∂ÂÆû‰æã
 * @param name1 Á¨¨‰∏Ä‰∏™ÂêçÁß∞Ôºå‰ºòÂÖàÁ∫ßÈ´ò‰∫é name2
 * @param name2 Á¨¨‰∫å‰∏™ÂêçÁß∞
 * @param defaultNode ÈªòËÆ§Ê∏≤ÊüìÂÜÖÂÆπÔºöÂΩì name1 Âíå name2 ÈÉΩ‰∏∫Á©∫Êó∂‰ºöÂêØÂä®ÈªòËÆ§ÂÜÖÂÆπÊ∏≤Êüì
 * @example renderContent(this, 'default', 'content')
 * @example renderContent(this, 'default', 'content', 'ÊàëÊòØÈªòËÆ§ÂÜÖÂÆπ')
 * @example renderContent(this, 'default', 'content', { defaultNode: 'ÊàëÊòØÈªòËÆ§ÂÜÖÂÆπ', params })
 */
export const renderContent = (
  instance: ComponentInternalInstance | null,
  name1: string,
  name2: string,
  options?: VNode | JSXRenderContext,
): any => {
  if (instance === null) {
    return h('', null);
  }
  const params = typeof options === 'object' && 'params' in options ? options.params : null;
  const defaultNode = typeof options === 'object' && 'defaultNode' in options ? options.defaultNode : options;
  const toParams = params ? { params } : undefined;
  const node1 = renderTNode(instance, name1, toParams);
  const node2 = renderTNode(instance, name2, toParams);
  const r = [undefined, null, ''].includes(node1) ? node2 : node1;
  return [undefined, null, ''].includes(r) ? defaultNode : r;
};
