import isBoolean from 'lodash/isBoolean';
import isObject from 'lodash/isObject';
// https://github.com/validatorjs/validator.js

import isDate from 'validator/lib/isDate';
import isEmail from 'validator/lib/isEmail';
import isEmpty from 'lodash/isEmpty';
import isURL from 'validator/lib/isURL';
import isNumber from 'lodash/isNumber';

import {
  CustomValidator,
  FormRule,
  ValueType,
  AllValidateResult,
  ValidateResultType,
  CustomValidateResolveType,
} from './type';

/**
 * ËÆ°ÁÆóÂ≠óÁ¨¶‰∏≤Â≠óÁ¨¶ÁöÑÈïøÂ∫¶Âπ∂ÂèØ‰ª•Êà™ÂèñÂ≠óÁ¨¶‰∏≤„ÄÇ
 * @param str ‰º†ÂÖ•Â≠óÁ¨¶‰∏≤
 * @param maxCharacter ËßÑÂÆöÊúÄÂ§ßÂ≠óÁ¨¶‰∏≤ÈïøÂ∫¶
 * @returns ÂΩìÊ≤°Êúâ‰º†ÂÖ•maxCharacterÊó∂ËøîÂõûÂ≠óÁ¨¶‰∏≤Â≠óÁ¨¶ÈïøÂ∫¶ÔºåÂΩì‰º†ÂÖ•maxCharacterÊó∂ËøîÂõûÊà™Âèñ‰πãÂêéÁöÑÂ≠óÁ¨¶‰∏≤ÂíåÈïøÂ∫¶„ÄÇ
 */
function getCharacterLength(str: string, maxCharacter?: number) {
  const hasMaxCharacter = isNumber(maxCharacter);
  if (!str || str.length === 0) {
    if (hasMaxCharacter) {
      return {
        length: 0,
        characters: str,
      };
    }
    return 0;
  }
  let len = 0;
  for (let i = 0; i < str.length; i++) {
    let currentStringLength = 0;
    if (str.charCodeAt(i) > 127 || str.charCodeAt(i) === 94) {
      currentStringLength = 2;
    } else {
      currentStringLength = 1;
    }
    if (hasMaxCharacter && len + currentStringLength > maxCharacter) {
      return {
        length: len,
        characters: str.slice(0, i),
      };
    }
    len += currentStringLength;
  }
  if (hasMaxCharacter) {
    return {
      length: len,
      characters: str,
    };
  }
  return len;
}

// `{} / [] / '' / undefined / null` Á≠âÂÜÖÂÆπË¢´ËÆ§‰∏∫ÊòØÁ©∫Ôºõ 0 Âíå false Ë¢´ËÆ§‰∏∫ÊòØÊ≠£Â∏∏Êï∞ÊçÆÔºåÈÉ®ÂàÜÊï∞ÊçÆÁöÑÂÄºÂ∞±ÊòØ 0 ÊàñËÄÖ false
export function isValueEmpty(val: ValueType): boolean {
  const type: string = Object.prototype.toString.call(val);
  const typeMap: Record<string, any> = {
    Date: '[object Date]',
  };
  if (type === typeMap.Date) {
    return false;
  }
  return isObject(val) ? isEmpty(val) : ['', undefined, null].includes(val);
}

const VALIDATE_MAP = {
  date: isDate,
  url: isURL,
  email: isEmail,
  required: (val: ValueType): boolean => !isValueEmpty(val),
  whitespace: (val: ValueType): boolean => !(/^\s+$/.test(val) || val === ''),
  boolean: (val: ValueType): boolean => isBoolean(val),
  max: (val: ValueType, num: number): boolean =>
    isNumber(val) ? val <= num : (getCharacterLength(val) as number) <= num,
  min: (val: ValueType, num: number): boolean =>
    isNumber(val) ? val >= num : (getCharacterLength(val) as number) >= num,
  len: (val: ValueType, num: number): boolean => getCharacterLength(val) === num,
  number: (val: ValueType): boolean => isNumber(val),
  enum: (val: ValueType, strs: Array<string>): boolean => strs.includes(val),
  idcard: (val: ValueType): boolean => /^(\d{18,18}|\d{15,15}|\d{17,17}x)$/i.test(val),
  telnumber: (val: ValueType): boolean => /^1[3-9]\d{9}$/.test(val),
  pattern: (val: ValueType, regexp: RegExp): boolean => regexp.test(val),
  // Ëá™ÂÆö‰πâÊ†°È™åËßÑÂàôÔºåÂèØËÉΩÊòØÂºÇÊ≠•Ê†°È™å
  validator: (val: ValueType, validate: CustomValidator): ReturnType<CustomValidator> => validate(val),
};

export type ValidateFuncType = (typeof VALIDATE_MAP)[keyof typeof VALIDATE_MAP];

/**
 * Ê†°È™åÊüê‰∏ÄÊù°Êï∞ÊçÆÁöÑÊüê‰∏ÄÊù°ËßÑÂàôÔºå‰∏ÄÁßçÊ†°È™åËßÑÂàô‰∏çÊª°Ë∂≥Âàô‰∏çÂÜçËøõË°åÊ†°È™å„ÄÇ
 * @param value ÂÄº
 * @param rule Ê†°È™åËßÑÂàô
 * @returns ‰∏§ÁßçÊ†°È™åÁªìÊûúÔºå‰∏ÄÁßçÊòØÂÜÖÁΩÆÊ†°È™åËßÑÂàôÁöÑÊ†°È™åÁªìÊûúÂì¶Ôºå‰∫åÁßçÊòØËá™ÂÆö‰πâÊ†°È™åËßÑÂàôÔºàvalidatorÔºâÁöÑÊ†°È™åÁªìÊûú
 */
export async function validateOneRule(value: ValueType, rule: FormRule): Promise<AllValidateResult> {
  let validateResult: CustomValidateResolveType | ValidateResultType = { result: true };
  const keys = Object.keys(rule);
  let vOptions;
  let vValidateFun: ValidateFuncType;
  for (let i = 0; i < keys.length; i++) {
    const key = keys[i];
    // ÈùûÂøÖÂ°´ÈÄâÈ°πÔºåÂÄº‰∏∫Á©∫ÔºåÈùûËá™ÂÆö‰πâËßÑÂàôÔºöÊó†ÈúÄÊ†°È™åÔºåÁõ¥Êé•ËøîÂõû true
    if (!rule.required && isValueEmpty(value) && !rule.validator) {
      return validateResult;
    }
    const validateRule: ValidateFuncType = VALIDATE_MAP[key];
    // ÊâæÂà∞‰∏Ä‰∏™Ê†°È™åËßÑÂàôÔºåÂàôÊó†ÈúÄÂÜçÊâæÔºåÂõ†‰∏∫ÂèÇÊï∞Âè™ÂÖÅËÆ∏ÂØπ‰∏Ä‰∏™ËßÑÂàôËøõË°åÊ†°È™å
    if (validateRule && (rule[key] || rule[key] === 0)) {
      // rule ÂÄº‰∏∫ true ÂàôË°®Á§∫Ê≤°ÊúâÊ†°È™åÂèÇÊï∞ÔºåÂè™ÊòØÂØπÂÄºËøõË°åÈªòËÆ§ËßÑÂàôÊ†°È™å
      vOptions = rule[key] === true ? undefined : rule[key];
      vValidateFun = validateRule;
      break;
    }
  }
  // @ts-ignore
  if (vValidateFun) {
    validateResult = await vValidateFun(value, vOptions);
    // Â¶ÇÊûúÊ†°È™å‰∏çÈÄöËøáÔºåÂàôËøîÂõûÊ†°È™å‰∏çÈÄöËøáÁöÑËßÑÂàô
    if (isBoolean(validateResult)) {
      return { ...rule, result: validateResult };
    }
    // Ê†°È™åÁªìÊûú‰∏∫ CustomValidateObjÔºåÂè™ÊúâËá™ÂÆö‰πâÊ†°È™åËßÑÂàô‰ºöÂ≠òÂú®ËøôÁßçÊÉÖÂÜµ
    if (isObject(validateResult)) {
      return validateResult;
    }
  }
  return validateResult;
}

// Âçï‰∏™Êï∞ÊçÆËøõË°åÂÖ®ËßÑÂàôÊ†°È™åÔºåÊ†°È™åÊàêÂäü‰πüÂèØËÉΩ‰ºöÊúâ message
export async function validate(value: ValueType, rules: Array<FormRule>): Promise<AllValidateResult[]> {
  const all = rules.map((rule) => validateOneRule(value, rule));
  const r = await Promise.all(all);
  return r;
}
