import { TdPopoverProps } from './type';
import { PropType } from 'vue';

export default {
  /** ÊòØÂê¶Âú®ÁÇπÂáªÂ§ñÈÉ®ÂÖÉÁ¥†ÂêéÂÖ≥Èó≠ËèúÂçï  */
  closeOnClickOutside: {
    type: Boolean,
    default: true,
  },
  /** Á°ÆËÆ§Ê°ÜÂÜÖÂÆπ */
  content: {
    type: [String, Function] as PropType<TdPopoverProps['content']>,
  },
  /** Ëß¶ÂèëÂÖÉÁ¥†ÔºåÂêå triggerElement */
  default: {
    type: [String, Function] as PropType<TdPopoverProps['default']>,
  },
  /** ÊµÆÂ±ÇÂá∫Áé∞‰ΩçÁΩÆ */
  placement: {
    type: String as PropType<TdPopoverProps['placement']>,
    default: 'top' as TdPopoverProps['placement'],
    validator(val: TdPopoverProps['placement']): boolean {
      if (!val) return true;
      return [
        'top',
        'left',
        'right',
        'bottom',
        'top-left',
        'top-right',
        'bottom-left',
        'bottom-right',
        'left-top',
        'left-bottom',
        'right-top',
        'right-bottom',
      ].includes(val);
    },
  },
  /** ÊòØÂê¶ÊòæÁ§∫ÊµÆÂ±ÇÁÆ≠Â§¥ */
  showArrow: {
    type: Boolean,
    default: true,
  },
  /** ÂºπÂá∫Ê∞îÊ≥°‰∏ªÈ¢ò„ÄÇ */
  theme: {
    type: String as PropType<TdPopoverProps['theme']>,
    default: 'dark' as TdPopoverProps['theme'],
    validator(val: TdPopoverProps['theme']): boolean {
      if (!val) return true;
      return ['dark', 'light', 'brand', 'success', 'warning', 'error'].includes(val);
    },
  },
  /** Ëß¶ÂèëÂÖÉÁ¥† */
  triggerElement: {
    type: [String, Function] as PropType<TdPopoverProps['triggerElement']>,
  },
  /** ÊòØÂê¶ÊòæÁ§∫Ê∞îÊ≥°Á°ÆËÆ§Ê°Ü */
  visible: {
    type: Boolean,
    default: undefined,
  },
  modelValue: {
    type: Boolean,
    default: undefined,
  },
  /** ÊòØÂê¶ÊòæÁ§∫Ê∞îÊ≥°Á°ÆËÆ§Ê°ÜÔºåÈùûÂèóÊéßÂ±ûÊÄß */
  defaultVisible: Boolean,
  /** Á°ÆËÆ§Ê°ÜÊòæÁ§∫ÊàñÈöêËóèÊó∂Ëß¶Âèë */
  onVisibleChange: Function as PropType<TdPopoverProps['onVisibleChange']>,
};
